class RegisterController {
  	constructor($scope,$rootScope, myConfig, $http) {
	  	"ngInject";
	  	this.$http = $http;
	    this.name = 'register';
	    this.$rootScope = $rootScope;
	    this.apiendpoint = myConfig.ApiEndpoint;

	    /*$scope.changeLanguage = function (key) {
	    	$translate.use(key);
	  	};*/
	  	let _this = this;
	  	this.getTerms().then(data => this.terms = data);
	  	this.getCourses().then(data => this.courseList = data);
	  	//this.test = "ahihihi";
	  	this.getStudentId().then(data => this.sid = data);
  	}

  	getCourses() {
    	return this.$http.get(this.apiendpoint+"/course")
      	.then(response => response.data);
	}

	getTerms() {
		return this.$http.get(this.apiendpoint+"/term")
      	.then(response => response.data);
	}

	getStudentId() {
		return this.$http.get(this.apiendpoint + "/student/" + this.$rootScope.globals.currentUser.username)
	      .then(response => response.data.id);
	}

	createRegistration(courseid) {
		var config = {
			headers: {'Content-Type': 'application/x-www-form-urlencoded'}
		}

	    var data = {courseId: courseid, studentId: parseInt(this.sid)};
	    //console.log(data);
	    this.$http.post(this.apiendpoint + "/courseregistration", data, config)
	      .then(response => {
	      	console.log(response.data);
			alert(response.data.message);
	    });
	}

	courseFilter() {
		
      	if (typeof this.termid !== "undefined" && this.termid != null) {
        	this.$http.get(this.apiendpoint + "/course/term/"+this.termid.id).then(response => this.courseList = response.data);
      	} else {
        	this.getCourses().then(data => this.courseList = data);
      	}
    }
}

export default RegisterController;



